import React, { useMemo } from 'react'
import { Svgs } from '../../Constants/Images'
import {
  Card,
  CardFront,
  Content,
  CardBack,
  Button,
  SemiCircle,
  ContainerSemiCircle
} from './cardIOS.styles'
// eslint-disable-next-line no-unused-vars
import { CardIOSProps, PositionType } from './cardIOS.types'

const CardIOS: React.FC<CardIOSProps> = (props) => {
  const { children, borderRadius, background, variant } = props
  // state
  const [flipped, setFlipped] = React.useState<boolean>(false)
  // memo
  const bRadius = useMemo(() => {
    if (variant === 'BOARDING_PASSES' || variant === 'STORE_CARDS') {
      return 10
    } else if (typeof borderRadius === 'number') {
      return borderRadius
    }
    return 0
  }, [variant, borderRadius])
  // functions
  const onClickFlip = () => {
    setFlipped(!flipped)
  }
  // render
  const renderButton = (position: PositionType) => {
    if (
      (position === 'front' && !flipped) ||
      (position === 'back' && flipped)
    ) {
      return (
        <Button onClick={onClickFlip}>
          <Svgs.MoreHorizIcon color='#0000008a' />
        </Button>
      )
    }
    return null
  }
  const renderSemiCircle = () => {
    if (variant === 'EVENT') {
      return (
        <ContainerSemiCircle>
          <SemiCircle background='#FFFFFF' />
        </ContainerSemiCircle>
      )
    }
    return null
  }
  return (
    <Card>
      <Content flipped={flipped}>
        {renderSemiCircle()}
        <CardFront
          background={background}
          borderRadius={borderRadius || bRadius}
        >
          {children?.[0]}
          {renderButton('front')}
        </CardFront>
        <CardBack
          background={background}
          flipped={flipped}
          borderRadius={borderRadius || bRadius}
        >
          {children?.[1]}
          {renderButton('back')}
        </CardBack>
      </Content>
    </Card>
  )
}

export { CardIOS }
